import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  TextField,
  Typography,
  CardMedia,
  Paper,
  IconButton,
  Slide,
} from "@material-ui/core/";
import SearchIcon from "@material-ui/icons/Search";

const api = {
  key: "3e24f8ec3ab92f67d68bd5902b5bd088",
  base: "https://api.openweathermap.org/data/2.5/weather?q=",
};

const useStyles = makeStyles((theme) => ({
  app: {
    width: "100vw",
    display: "flex",
    flexDirection: "column",
    justifyContent: "flex-start",
    alignItems: "center",
    textAlign: "center",
    background:
      "radial-gradient(circle, rgba(27,131,255,1) 0%, rgba(23,134,255,1) 60%, rgba(115,184,255,1) 100%)",
  },
  icon: {
    height: 250,
    width: 250,
    margin: "-25px 0 -50px 0",
  },
  text: {
    color: "white",
    textShadow: "2px 2px 4px rgba(0, 0, 0, 1)",
  },
  time: {
    color: "white",
    textShadow: "2px 2px 4px rgba(0, 0, 0, 1)",
    fontSize: "3rem",
  },
  temp: {
    color: "white",
    textShadow: "2px 2px 4px rgba(0, 0, 0, 1)",
    fontSize: "6rem",
    marginRight: "-1.5rem",
  },
  search: {
    color: "white",
  },
  paper: {
    marginTop: "auto",
    backgroundColor: "rgba(255, 255, 255, 0.5)",
  },
}));

function App() {
  const [query, setQuery] = useState("");
  const [weather, setWeather] = useState({});
  const [height, setHeight] = useState(window.innerHeight);
  const [animated, setAnimated] = useState(false);

  window.addEventListener("resize", () => {
    setHeight(window.innerHeight);
  });

  let divHeightStyle = {
    height: height,
  };

  const classes = useStyles();

  const search = () => {
    fetch(`${api.base}${query}&units=metric&lang=ru&appid=${api.key}`)
      .then((res) => res.json())
      .then((result) => {
        setWeather(result);
        setQuery("");
        console.log(result);
      })
      .catch((e) => {
        console.log(e);
      });
    setAnimated(true);
  };

  const animate = () => {
    setAnimated(false);
    setTimeout(() => {
      search();
    }, 500);
  };

  const days = [
    "Воскресенье",
    "Понедельник",
    "Вторник",
    "Среда",
    "Четверг",
    "Пятница",
    "Суббота",
  ];
  const months = [
    "Января",
    "Февраля",
    "Марта",
    "Апреля",
    "Мая",
    "Июня",
    "Июля",
    "Августа",
    "Сентября",
    "Октября",
    "Ноября",
    "Декабря",
  ];

  const day = days[new Date().getDay()];
  const date = new Date().getDate();
  const month = months[new Date().getMonth()];
  const minutes = () => {
    if (new Date().getMinutes() < 10) {
      return "0" + new Date().getMinutes();
    } else {
      return new Date().getMinutes();
    }
  };
  const time = `${new Date().getHours()}:${minutes()}`;

  return (
    <div className={classes.app} style={divHeightStyle}>
      <Typography
        className={classes.text}
      >{`${day}, ${date} ${month}`}</Typography>
      <Typography className={classes.time}>{time}</Typography>
      {typeof weather.main != "undefined" ? (
        <div>
          <Typography className={classes.text}>{weather.name}</Typography>
          <Typography className={classes.text}>
            {weather.weather[0].description.charAt(0).toUpperCase() +
              weather.weather[0].description.slice(1)}
          </Typography>
          <Slide direction="right" in={animated} mountOnEnter unmountOnExit>
            <CardMedia
              image={`http://openweathermap.org/img/wn/${weather.weather[0].icon}@4x.png`}
              className={classes.icon}
            ></CardMedia>
          </Slide>
          <Typography className={classes.temp}>
            {Math.round(weather.main.temp)}°
          </Typography>
        </div>
      ) : (
        <div></div>
      )}

      <Paper className={classes.paper} elevation={3} variant="outlined">
        <TextField
          variant="filled"
          autoFocus={true}
          className={classes.search}
          onChange={(e) => setQuery(e.target.value)}
          value={query}
          onKeyPress={(e) => e.key === "Enter" && animate()}
        />
        <IconButton type="submit" onClick={animate}>
          <SearchIcon />
        </IconButton>
      </Paper>
    </div>
  );
}

export default App;
